 export default {
     name: 'app',
     
     data () {
	 return {
	     loading: false,
	     start_loading: false,
	     show_breathing: false,
	     interval: false,
	     currentTime: 0,
	     'uebungen': [
		 {titel: "Hipflex",video: "Hipflex.mp4",thumb:'thumbs/thumb7.jpg', pause_at: 11, beweglichkeitszirkel: true,atemzuege_einsteiger: 5,atemzuege_fortgeschritten: 10,
		  captions: [{start: 0, end: 4, text: "Hinsetzen"},
			     {start: 2, end: 8, text: "next sample"},
			     {start: 4, end: 8, text: "next sample"}]},
		 {titel: "Split",video: "Split.mp4",thumb:'thumbs/thumb6.jpg',beweglichkeitszirkel: true,atemzuege_einsteiger: 5,atemzuege_fortgeschritten: 10},
		 {titel: "Chest",video: "Chest.mp4",thumb:'thumbs/thumb1.jpg',beweglichkeitszirkel: true,atemzuege_einsteiger: 5,atemzuege_fortgeschritten: 10},
		 {titel: "Back",video: "Back.mp4",thumb:'thumbs/thumb3.jpg',beweglichkeitszirkel: true,atemzuege_einsteiger: 5,atemzuege_fortgeschritten: 10,
		  captions: [{start: 3, text: "Hände am Gerät sichern"},
			     {start: 4, text: "Beine strecken"},
			     {start: 5, text: "Zehenspitzen zeigen nach unten"},
			     {start: 6, text: "Oberkörper zurücklehnen"},
			     {start: 7, text: "Füße und Beine bleiben gestreckt"},
			     {start: 8, text: "Kopf zur Wirbelsäule verlängern"},
			     {start: 9, text: "Kinn zur Brust"},
			     {start: 10, text: "Atmen"}],
		  pause_at: 10.5},
		 {titel: "Glut",video: "Glut.mp4",thumb:'thumbs/thumb8.jpg',beweglichkeitszirkel: true,atemzuege_einsteiger: 5,atemzuege_fortgeschritten: 10},
		 {titel: "Lat",video: "Lat.mp4",thumb:'thumbs/thumb5.jpg',beweglichkeitszirkel: true,atemzuege_einsteiger: 5,atemzuege_fortgeschritten: 10},
		 {titel: "Calf",video: "Calf.mp4",thumb:'thumbs/thumb2.jpg',beweglichkeitszirkel: true,atemzuege_einsteiger: 5,atemzuege_fortgeschritten: 10},
		 {titel: "Trap",video: "Trap.mp4",thumb:'thumbs/thumb4.jpg',beweglichkeitszirkel: true,atemzuege_einsteiger: 5,atemzuege_fortgeschritten: 10}
	     ],
	     'modus': {
		 einsteiger: {
		     atemzuege: 5
		 },
		 fortgeschritten: {
		     atemzuege: 10
		 }
	     },
	     video_pause_complete: false,
	     aktive_uebung: false,
	     step_progress: 0,
	     atmungen: [{nummer: 1, aktiv:false, erledigt: false},
			{nummer: 2, aktiv:false, erledigt: false},
			{nummer: 3, aktiv:false, erledigt: false},
			{nummer: 4, aktiv:false, erledigt: false},
			{nummer: 5, aktiv:false, erledigt: false}]
	 }
     },
     
     components: {},

     created: function () {
	//  process.env.NODE_ENV === 'production'
        // ? 'https://app.maypower.services/skyline-videos'
         // : '',
	 window.addEventListener("resize", this.resize_windows);
     },

     methods: {
	 'aktiviere': async function (uebung) {
	     this.aktive_uebung = uebung;
	     this.video_pause_complete = false;
	     var url = this.format_video_url(uebung.video);
	     this.$refs.playerSource.setAttribute('src', url);
	     this.$refs.player.load();
	     this.$refs.player.onended = (e) => {
		 this.aktive_uebung = false;
	     };
	     this.$refs.player.ontimeupdate = (e) => {
		 this.currentTime = this.$refs.player.currentTime;
		 if (this.currentTime <= 1) this.video_pause_complete = false; // reset
		 
		 if (!this.video_pause_complete && this.aktive_uebung.pause_at <= this.$refs.player.currentTime) {
		     this.$refs.player.pause();
		     this.start_breathing();
		 }
	     };
	     this.loading = false;
	     this.start_uebung();
	 },

	 resize_windows() {
	     var videoHeight = window.innerHeight - window.innerHeight * 0.2;
	     if (window.innerWidth > 800) {
		 if (this.aktive_uebung) {
		     this.$refs.rightSide.style.width = videoHeight + 80 + 'px';
		     this.$refs.leftSide.style.width = window.innerWidth - videoHeight + 'px';
		 }
	     } else {
		 if (this.aktive_uebung) {
		     this.$refs.rightSide.style.width = '100%';
		     this.$refs.leftSide.style.width = '100%';
		 }
	     }
	 },

	 start_uebung () {
	     /*this.start_loading = true;
	     this.sleep(2300).then(() => {
		 this.start_loading = false;
		 this.$refs.player.play();
		 });*/
	     this.$refs.player.play();
	 },

	 start_breathing () {
	     this.reset_breathing();
	     var i = 0, scale = 1,inow = 0, ilast = 0;
	     this.show_breathing = true;
	     var breathing = () => {
		 scale = (scale == 1 ? 4 : 1);
		 this.$refs.breathing_indicator.style = 'transform: scale('+ scale + '); background: #fff;';
		 if (i == 10) {
		     clearInterval(this.interval);
		     this.$refs.player.play();
		     this.video_pause_complete = true;
		     this.show_breathing = false;
		     this.reset_breathing();
		 } else if (i%2 == 0) {
		     inow = i / 2;
		     if (this.atmungen[inow])
			 this.atmungen[inow].aktiv = true;
		     ilast = inow - 1;
		     if (this.atmungen[ilast]) {
			 this.atmungen[ilast].erledigt = true;
			 this.atmungen[ilast].aktiv = false
		     }
		 }
		 i++;
	     };
	     
	     setTimeout(() => {
		 breathing();
		 this.interval = setInterval(breathing, 3000);
	     }, 300);
	 },

	 caption_is_visible(caption) {
	     return (caption.start < this.currentTime);
	 },

	 caption_is_last(caption) {
	     var aktive_captions = this.aktive_uebung.captions.filter((caption) => {
		 return this.caption_is_visible(caption);
	     });
	     return caption == aktive_captions[aktive_captions.length - 1];
	 },

	 reset_breathing() {
	     var i;
	     if (this.interval) clearInterval(this.interval);
	     for(i=0; i<= this.atmungen.length; i++) {
		 if (this.atmungen[i]) {
		     this.atmungen[i].erledigt = false;
		     this.atmungen[i].aktiv = false;
		 }
	     }
	 },

	 reset_video () {
	     this.$refs.player.pause();
	     this.$refs.video_pause_complete = false;
	 },

	 sleep (ms) {
	     return new Promise(resolve => setTimeout(resolve, ms))
	 },

	 format_video_url (url) {
	     return (window.location.host === "app.maypower.services" ? "https://app.maypower.services/skyline-videos/" : 'http://localhost/skyline-videos/') + url;
	 }
     },

     watch: {
	 'aktive_uebung' (val) {
	     if (val == false) {
		 this.reset_video();
		 this.show_breathing = false;
		 this.start_loading = false;
		 this.reset_breathing();
		 this.resize_windows();
	     }
	 }
     },
     
     computed: {
	 'uebungsliste' () {
	     return this.uebungen;
	 }
     }
 }
